import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApicallService } from 'src/app/core/services/apicall/apicall.service';
import { GlobalService } from 'src/app/core/services/global/global.service';
@Component({
  selector: 'app-create-mtp',
  templateUrl: './create-mtp.page.html',
  styleUrls: ['./create-mtp.page.scss'],
})
export class CreateMtpPage implements OnInit {
  public ass_dcr: any ;
  public bind: any= '2022-11-';
  public selected_mtp:any;
  public doctors: any;
  public add_doctor:any={d_id:null , d_name:'' , doc_q:'' , h_name:'' , contact_no:''}

  public allCities : any;
  public medicen: any ;
 public selectedmedican:any; 
 public doctorData:any; 
 public prodcutId:any = [];
 public data:any = {mtpdata:{mr_id: '',c_id: '', d_id: '',date: '', month:'', year:''}, product:[]}
  constructor( public router:Router,  public apicall : ApicallService, public global : GlobalService) { }

  ngOnInit() {
const month = ["January","February","March","April","May","June","July","August","September","October","November","December"];
    console.log(month.length);
    const d = new Date();
    let name = month[d.getMonth() + 1];
    console.log(name);
    let currentname = month[d.getMonth()];
    console.log(currentname);
    this.data.mtpdata.month = currentname;
    console.log(this.data.mtpdata.month);
    // if(currentname == "December") {
    //   const x = new Date();
    //   let year = [x.getFullYear() + 1]
    //   console.log(year[0]);
    //   this.data.mtpdata.year = year[0];
    //   console.log(this.data.mtpdata.year);
    // } 
    // else {
      const x = new Date();
      let year = [x.getFullYear()]
      console.log(year[0]);
      this.data.mtpdata.year = year[0];
      console.log(this.data.mtpdata.year);
    // }
    this.getAllCities();
    this.getAllProducts();
    this.global.MrLoginData.subscribe(res =>{
      this.data.mtpdata.mr_id = res.user.mr_id;
      console.log(this.data.mtpdata.mr_id);
    });
  }

  async getAllCities() {
    await this.apicall.api_getCities();
    this.global.GetCities.subscribe(res => {
        this.allCities = res;
    })
}

async selectCity(event){
  console.log(event.target.value);
  this.data.mtpdata.c_id = event.target.value.id;
  const x = event.target.value.name;
  await this.apicall.api_getDoctorbycity(x);
  this.global.GetDoctorbycity.subscribe(res=>{
    this.doctorData = res;
    console.log(this.doctorData);
  })
}

async selectDoctor(event){
  console.log(event.target.value);
  this.data.mtpdata.d_id = event.target.value;
}

  get_medican(item){
    this.selectedmedican = item;
    console.log(item)
  }
 
  async  getAllProducts(){
    await this.apicall.api_getproduct();
    this.global.AllProduct.subscribe(res=>{
        this.medicen = res;
        console.log(this.medicen);
    });
     }
  
     getData(item) {
      console.log(item);
      this.prodcutId.push(item);
      console.log(this.prodcutId);
      if(item === undefined) {
        this.data.product.pop();
      }
      else {
        this.data.product.push({p_id:item});
      }
      console.log(this.data)

    }

  get_date($event){
    console.log($event.target.value)
    this.data.mtpdata.date = $event.target.value;
    console.log(this.data.mtpdata.date)
  }
  adddata(){ 
    console.log(this.data);
    this.apicall.api_CreateMtp(this.data);
    // this.router.navigate(['mtp'])
  }
  

}
